.working {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: fit-content;
}

.working h1 {
  width: 600px;
  font-family: var(--font-gloock);
  font-size: 6rem;
  letter-spacing: 0.1em;
  line-height: 1.5;
  text-transform: uppercase;
  color: #8c442a;
}
